@charset "utf-8";

$padd: 3.5rem;
$paddmed: 2.5rem;
$padsmall: 1.5rem;
$fontsize: 1.2rem;

$bg: #ffffff;
$blue: #4047ee;
$offWhite: #f5f9fc;
$lightblue: #8d9bb8;
$white: $bg;
$a-color: $blue;
$a-footer: #8d9bb8;

// 3F45E9, 8D9BB8, E0E3EF

html,
body {
  background-color: $bg;
  line-height: 24pt;
  letter-spacing: 0.5px;
  -webkit-text-size-adjust: 100%;
  font-variant-ligatures: none;
  -webkit-font-variant-ligatures: none;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
  text-shadow: rgba(0, 0, 0, 0.01) 0 0 1px;
  font-family: "Exo", Arial, Helvetica, sans-serif;
}

.site-header {
  margin: 6vh 0;
  .secondary.pointing.menu {
    border: 0;
  }
}

.post-title {
  padding: 0;
  margin: 0;
}

.post-subtitle {
  margin: 0 0 25px 0;
}

.masthead {
  .column {
    &.text {
      padding-top: 6rem !important;
      padding-bottom: 6rem !important;
    }
  }
  h1 {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 4.5rem !important;
  }
  h2 {
    font-size: 1.6rem;
    line-height: 2.4rem;
  }
}

.page-text {
  margin: 10vh 0;
  .author {
    font-style: italic;
  }
  p {
    line-height: 1.75em !important;
  }
}

.footer {
  .slogan {
    font-size: 12px;
  }
}

.ui {
  &.text {
    font-size: $fontsize;
  }

  .segment.centered.basic {
    margin: 0 auto;
  }

  .big.divider {
    margin: 2rem 0;
  }

  .label {
    font-weight: normal;
  }
}
a:not(.button) {
  color: #666 !important;
  text-decoration: none;

  :hover {
    color: #666 !important;
    text-decoration: underline !important;
  }
}

.page-default {
  .masthead {
    min-height: 400px;
  }
}

.path-front {
  .sub.header {
    line-height: 1.5em !important;
  }
  .ui.header > .icon {
    vertical-align: top !important;
  }
}

article {
  a:not(.button) {
    color: $a-color !important;
    text-shadow: 0 1px 0 lighten($a-color, 60%);
    text-decoration: none;
    border-bottom: 1px dashed lighten($a-color, 15%);
    position: relative;
    padding: 0 0.2em;
    transition: all 250ms;
    z-index: 1000;
    &:before {
      content: "";
      z-index: -1;
      width: 100%;
      height: 0%;
      background: rgba(lighten($a-color, 20%), 0.16);
      bottom: 0;
      left: 0;
      position: absolute;
      transition: height 250ms;
    }
    &:hover {
      border-color: transparent;
      &:before {
        height: 100%;
      }
    }
  }
}

i:focus {
  border: 0 none;
  outline: 0 none;
}

.centered {
  text-align: center;
}

.ohidden {
  display: none;
  visibility: hidden;
}

@media only screen and (max-width: 767px) {
  [class*="mobile hidden"],
  [class*="tablet only"]:not(.mobile),
  [class*="computer only"]:not(.mobile),
  [class*="large screen only"]:not(.mobile),
  [class*="widescreen only"]:not(.mobile),
  [class*="or lower hidden"] {
    display: none !important;
  }
  .ui.horizontal.divider {
    white-space: normal !important;
    display: inline-block;
  }
  .secondary {
    .item {
      padding: 0 !important;
    }
  }
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  [class*="mobile only"]:not(.tablet),
  [class*="tablet hidden"],
  [class*="computer only"]:not(.tablet),
  [class*="large screen only"]:not(.tablet),
  [class*="widescreen only"]:not(.tablet),
  [class*="or lower hidden"]:not(.mobile) {
    display: none !important;
  }
}

@media only screen and (min-width: 992px) and (max-width: 1199px) {
  [class*="mobile only"]:not(.computer),
  [class*="tablet only"]:not(.computer),
  [class*="computer hidden"],
  [class*="large screen only"]:not(.computer),
  [class*="widescreen only"]:not(.computer),
  [class*="or lower hidden"]:not(.tablet):not(.mobile) {
    display: none !important;
  }
}

@media only screen and (min-width: 1200px) and (max-width: 1919px) {
  [class*="mobile only"]:not([class*="large screen"]),
  [class*="tablet only"]:not([class*="large screen"]),
  [class*="computer only"]:not([class*="large screen"]),
  [class*="large screen hidden"],
  [class*="widescreen only"]:not([class*="large screen"]),
  [class*="or lower hidden"]:not(.computer):not(.tablet):not(.mobile) {
    display: none !important;
  }
}

@media only screen and (min-width: 1920px) {
  [class*="mobile only"]:not([class*="widescreen"]),
  [class*="tablet only"]:not([class*="widescreen"]),
  [class*="computer only"]:not([class*="widescreen"]),
  [class*="large screen only"]:not([class*="widescreen"]),
  [class*="widescreen hidden"],
  [class*="widescreen or lower hidden"] {
    display: none !important;
  }
}

.ui.menu.grid > .computer.only.row {
  padding: 0;
}

.social {
  i:hover {
    text-decoration: none !important;
  }
}
::selection {
  background-color: $blue;
  color: $white;
}
.cuticon {
  color: $bg;
  padding-top: 20px;
}
.cta-wide {
  background-color: $lightblue;
  h3.cta {
    font-size: 3rem !important;
    color: $white;
    font-weight: 700;
  }
}

.container {
  &.padded {
    padding: 10vh 0 !important;
  }
  h1 {
    font-size: 3rem;
  }
  h2 {
    font-size: 2rem;
  }
  h3 {
    font-size: 1.5rem;
  }
}

.footer-wrap {
  background-color: $offWhite;
}
.header {
  &.clickable {
    cursor: pointer;
    padding: 5px;
    &.active,
    &:hover {
      color: $blue;
      position: relative;
      .sub.header {
        color: $blue;
      }
    }
  }
}

.right {
  .clickable {
    &.active:after,
    &:hover:after {
      left: 100%;
      top: 50%;
      border: solid transparent;
      content: " ";
      height: 0;
      width: 0;
      position: absolute;
      pointer-events: none;
      border-left-color: $blue;
      border-width: 10px;
      margin-top: -10px;
    }
  }
}
.left {
  .clickable {
    &.active:after,
    &:hover:after {
      right: 100%;
      top: 50%;
      border: solid transparent;
      content: " ";
      height: 0;
      width: 0;
      position: absolute;
      pointer-events: none;
      border-right-color: $blue;
      border-width: 10px;
      margin-top: -10px;
    }
  }
}

.home-masthead {
  background-image: url(/assets/img/bg-waves-white2.svg);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
}

.react-rotating-text-cursor {
  animation: blinking-cursor 0.8s cubic-bezier(0.68, 0.01, 0.01, 0.99) 0s
    infinite;
}

@keyframes blinking-cursor {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.page-howitworks {
  h2 {
    font-weight: 900 !important;
    font-size: 2.5rem !important;
  }
  .grid {
    margin: 8vh 0 !important;
  }
  .buttons {
    margin: 5vh 0;
    .button {
      min-width: 240px;
    }
  }
}

.app-download {
  padding: 5vh 0;
  &.light-blue {
    background-color: $lightblue;
  }
  p,
  h1 {
    color: $white !important;
  }
}

.ui.grid.container > .column,
.ui.container .grid:not(.nested) > .column {
  @media only screen and (max-width: 767px) {
    padding-left: 0 !important;
    padding-right: 0 !important;
    .ui.stackable.grid {
      margin-left: 0 !important;
      margin-right: 0 !important;
    }
  }
}
